}
html, body, #root {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 15px;
  background: #f1f1f1;
  font-family: 'Courier New', Courier, monospace;
}
.home-conatiner {
    width: 100%;
    height: calc(100% - 70px);
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.home-conatiner h1 {
    color: #009688;
    text-align: center;
    font-weight: 600;
}
.userDetails-primary {
    display: -webkit-flex;
    display: flex;
}
.userDetails-primary-lhs {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    padding: 0px 20px;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.userDetails-primary-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 20px;
}
.userDetails-primary-img img{
    max-width: 100%;
}
.userDetails-primary-username {
    color: #4c4c4c;
    font-size: 18px;
    font-weight: 600;
}
.userDetails-primary-rhs {
    padding: 0px 20px;
}
.userDetails-primary-row {
    display: -webkit-flex;
    display: flex;
    padding-bottom: 15px;
    color: #4c4c4c;
    font-size: 16px;
}
.userDetails-primary-row:last-child {
    padding: 0;
}
.userDetails-primary-row .userDetails-primary-col:first-child {
    font-weight: 600;
}
.userDetails-primary-row .userDetails-primary-col:last-child {
