}
.tab-item {
    padding: 10px 20px;
    font-size: 1.1rem;
    color: #4c4c4c;
    cursor: pointer;
    border-bottom: 4px solid transparent;
}
.tab-item:hover {
    background: rgba(0, 0, 0, 0.05);
}
.tab-item.active {
    color: #000;
    border-bottom: 4px solid #009688;
    background: rgba(0, 0, 0, 0.02);
}

.userCard {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 10px;
    margin: 10px;
    box-shadow: 2px 2px 8px 0px rgba(0, 0, 0, 0.15);
    border-radius: 3px;
    transition: 300ms all ease-in-out;
}
.userCard:hover {
    cursor: pointer;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 2px 2px 12px 0px rgba(0, 0, 0, 0.2);
}
.userCard-link {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}
.userCard-lhs {
    display: -webkit-flex;
    display: flex;
    max-width: 160px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    padding: 0px 20px;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.userCard-img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 20px;
}
.userCard-img img{
    max-width: 100%;
}
.userCard-username {
    color: #4c4c4c;
    font-size: 18px;
    font-weight: 600;
    max-width: 120px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.userCard-rhs {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 0px 20px;
}
.userCard-row {
    display: -webkit-flex;
    display: flex;
    padding-bottom: 15px;
    color: #4c4c4c;
    font-size: 16px;
}
.userCard-row:last-child {
    padding: 0;
}
.userCard-row .userCard-col:first-child {
    font-weight: 600;
}
.userCard-row .userCard-col:last-child {
    font-weight: 500;
    margin-left: 10px;
}
.loader {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 200px;
}
.userFollowers-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    padding: 20px;
}
.githubCalendar {
    padding: 40px 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 200px;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.repoCard {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    min-width: calc(50% - 20px);
    padding: 10px;
    margin: 10px;
    border-radius: 3px;
    border: 2px solid rgba(0, 0, 0, 0.2);
    transition: 300ms all ease-in-out;
}
.repoCard:hover {
    cursor: pointer;
    border: 2px solid #009688;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
}
.repoCard .inner {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.repoCard .content {
    margin: 10px 0;
}
.repoCard .content .description {
    font-size: 15px;
    color: #4c4c4c;
    font-weight: 500;
}
.repoCard .header {
    font-size: 20px;
    color: #009688;
    background: #fff;
    height: auto;
    font-weight: 600;
}
.repoCard .sub-header {
    margin: 10px 0 5px;
}
.repoCard .footer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.repoCard .footer div {
    margin-right: 10px;
}
.repoCard .footer div:last-child {
    margin: 0;
}
.pagination {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow: hidden;
    overflow-x: auto;
}
.pagination-list {
    max-width: 90%;
    text-align: center;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
}
.pagination-list li:first-child {
    margin-left: 0px;
}
.pagination-list li:last-child {
    margin-right: 0px;
}
.pagination-list li {
    height: 30px;
    width: 30px;
    text-align: center;
    font-size: 16px;
    line-height: 30px;
    font-weight: 600;
    color: #4c4c4c;
    margin: 0 5px;
    border-radius: 3px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    list-style-type: none;
}
.pagination-list li:hover {
    cursor: pointer;
    background: #f1f1f1;
    color: #000;
}
.pagination-list li.active {
    background: #009688;
    color: #fff;
    border: 1px solid #009688;
}
.commits-table {
    width: 100%;
    text-align: center;
}
.commits-table th, td {
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.5);
}
.commits-table tr:nth-child(2n) {
    background: #f5f5f5;
}
.commits-table tr {
    /* border: 1px solid rgba(0, 0, 0, 0.5); */
}
.commits-modal {
    background: white;
    overflow-y: auto;
    overflow-x: hidden;
    width: 1080px;
    outline: none;
    min-height: 550px;
    max-height: 650px;
    border-radius: 6px;
    position: relative;
    box-shadow: 0 15px 15px 0 rgba(0, 0, 0, 0.12), 0 5px 15px 0 rgba(0, 0, 0, 0.20);
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.modal-header {
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.modal-header .repo-name {
    font-size: 20px;
    color: #000;
    font-weight: 600;
}
.modal-header .close {
    cursor: pointer;
}
.modal-content {
    padding: 15px 40px;
}
.modal-footer {
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.repos-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    padding: 20px;
}

.userDetails-container {
    max-width: 1080px;
    width: 100%;
    margin: 70px auto 0;
    padding: 50px 0;
}
.userDetails-content-primary,
.userDetails-content-secondary {
    padding: 20px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 2px 3px 4px 2px rgba(0, 0, 0, 0.1);
    margin-bottom: 40px;
}
.userDetails-content-secondary {
    padding: 0;
}
.loading-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    min-height: 300px;
}
header {
  width: 100%;
  height: 70px;
  display: -webkit-flex;
  display: flex;
  background: #ccc;
  position: fixed;
  top: 0;
  z-index: 1;
}
.header-nav {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.header-search {
  width: 300px;
  position: relative;
}
.header-search input {
  width: 100%;
  height: 40px;
  font-size: 1rem;
  border-radius: 4px;
  outline: none;
  box-shadow: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding-left: 10px;
}
.users-list-container {
  position: absolute;
  width: 100%;
  left: 0;
  top: 40px;
  max-height: 400px;
  overflow: hidden;
  overflow-y: auto;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .13);
  border-radius: 4px;
  background: #fff;
}
.users-list-items {
  padding: 0;
  margin: 0;
}
.users-list-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  list-style-type: none;
}
.users-list-item:hover {
  cursor: pointer;
  background: rgba(0, 0, 0, 0.05);
}
.users-list-item-img {
  width: 40px;
  height: 40px;
  overflow: hidden;
  border-radius: 50%;
}
.users-list-item-img img {
  max-width: 100%;
}
.users-list-item-name {
  font-size: 1.1rem;
  margin-left: 10px;
  font-weight: 500;
  color: #4c4c4c;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
p {
  margin: 0;
}

.App {
  text-align: center;
}

.backButton {
  color: #fff;
  font-weight: 600;
  text-decoration: none;
}

.backButton::before {
  content: '';
  border: solid #fff;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 4px;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}


